@use "../variables" as *;
@use "../mixins" as *;

.ErrorPage {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: calc(100vh - 92px);
  gap: 10px;
  h1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: "Questa Grande", serif;
    font-style: italic;
    font-weight: 400;
    font-size: clamp(2.875rem, 0.125rem + 13.75vw, 12.5rem);
    text-align: center;
    text-transform: uppercase;
    color: $Argent;
  }
  .ErrorPageContainer {
    display: flex;
    flex-direction: column;
    gap: 10px;
    > * {
      display: flex;
    }
    h2 {
      @include HeadingThreeProject;
      font-size: clamp(1.3125rem, 1.1161rem + 0.9821vw, 2rem);
    }
    p {
      @include BodyText;
    }
    .ReturnHomeButton {
      font-family: "Anaheim", serif;
      text-transform: uppercase;
      text-decoration: none;
      background-color: $Khaki;
      border-radius: 40px;
      border: 1px solid $Khaki;
      color: $SmokyBlack;
      padding: 5px 10px;
      transition: $Transition-03;
      width: min-content;
      cursor: none;
      &:hover {
        background-color: $SmokyBlack;
        color: $Khaki;
        border: 1px solid $Khaki;
        transition: $Transition-03;
      }
    }
  }
}
