@use "./variables" as *;

@mixin HeadingOneTitle {
  font-family: "Questa Grande", serif;
  font-style: normal;
  font-weight: 400;
  font-size: clamp(4.25rem, -9.5rem + 24.4444vw, 12.5rem);
  text-align: right;
  text-transform: uppercase;
  color: $Argent;
}

@mixin HeadingOne {
  display: flex;
  flex-direction: column;
  font-family: "Questa Grande", serif;
  font-style: italic;
  font-weight: 400;
  font-size: clamp(2.875rem, 0.125rem + 13.75vw, 12.5rem);
  text-align: center;
  text-transform: uppercase;
  color: $Argent;
}

@mixin HeadingTwoAccentLetter {
  font-family: "Monsieur La Doulaise", cursive;
  font-style: normal;
  font-size: clamp(2.25rem, 1.205rem + 3.8vw, 4.625rem);
  font-weight: 400;
  line-height: 75px;
}

@mixin HeadingTwoProject {
  font-family: "Questa Grande", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  border-bottom: 1px solid red;
}

@mixin HeadingThreeProject {
  font-family: "Questa Grande", serif;
  font-style: normal;
  font-weight: 400;
  font-size: clamp(1rem, 0.7143rem + 1.4286vw, 2rem);
  text-transform: uppercase;
}

@mixin BodyText {
  font-family: "Anaheim", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
}

@mixin ProjectStructure {
  .Projects {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}

@mixin HeadlineStyle {
  flex-direction: column;
  gap: 10px;
  border-top: 1px solid $Argent;
  > * {
    display: flex;
  }
  h2 {
    font-family: "Questa Grande", serif;
    font-style: normal;
    font-weight: 400;
    font-size: clamp(1.5rem, 0.7864rem + 3.5682vw, 4rem);
    text-transform: uppercase;
    color: $Argent;
  }
}
